.item-watches {
  border: 1px solid grey;
  border-radius: 5px;
  padding: 0.8rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
}

.item-watches__title {
  font-size: 0.9rem;
  text-align: center;
  margin-bottom: 5px;
}

.item-watches__time-digit-wrap {
  display: flex;
  justify-content: center;
}

.item-watches__time-digit {
  width: 30%;
  color: green;
  margin-bottom: 0.4rem;
  font-size: 0.6rem;
}

.item-watches__clock-face {
  margin: 0 auto;
}

.clock-face {
  width: 120px;
  height: 120px;
  border: 1px solid black;
  border-radius: 50%;
}

.item-watches__btn-remove {
  position: absolute;
  display: block;
  top: 5px;
  right: 5px;
  width: 25px;
  height: 25px;
  border: 1px solid grey;
  border-radius: 50%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: box-shadow 0.3s;
}

.item-watches__btn-remove:hover {
  box-shadow: 0 0 5px 0 grey;
}

.item-watches__btn-remove::before,
.item-watches__btn-remove::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 17px;
  height: 2px;
  background-color: red;
}

.item-watches__btn-remove::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.item-watches__btn-remove::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.clock-face {
  position: relative;
}

.clock-face::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: black;
  transform: translate(-50%, -50%);
}

.clock-face__arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: black;
  transform-origin: bottom;
}

.clock-face__arrow::after {
  content: "";
  position: absolute;
  top: -2px;
  left: 0px;
  width: 0px;
  height: 0px;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-bottom: 6px solid black;
}

.clock-face__hour-arrow {
  width: 2px;
  height: 40px;
  transform: translate(-50%, -100%) rotate(60deg);
}

.clock-face__hour-arrow::after {
  transform: translate(-35%, 0);
}

.clock-face__minute-arrow {
  width: 2px;
  height: 50px;
  transform: translate(-50%, -100%) rotate(0deg);
}

.clock-face__minute-arrow::after {
  transform: translate(-40%, 0);
}

.clock-face__second-arrow {
  width: 1px;
  height: 50px;
  background-color: gray;
  transform: translate(-50%, -100%) rotate(150deg);
}

.clock-face__second-arrow::after {
  display: none;
}

@media (min-width: 991px) {
  .item-watches {
    flex: 0 1 auto;
    width: calc(25% - 0.8rem);
  }

  .item-watches:nth-child(4n) {
    margin-right: 0;
  }
}

@media (min-width: 768px) and (max-width: 990px) {
  .item-watches {
    flex: 0 1 auto;
    width: calc(33.33% - 0.8rem);
  }

  .item-watches:nth-child(3n) {
    margin-right: 0;
  }
}

@media (min-width: 561px) and (max-width: 767px) {
  .item-watches {
    flex: 0 1 auto;
    width: calc(50% - 0.8rem);
  }

  .item-watches:nth-child(2n) {
    margin-right: 0;
  }
}

@media (max-width: 560px) {
  .item-watches {
    display: block;
    width: 100%;
  }

  .item-watches {
    margin-right: 0;
  }
}
